'use strict';

var template = (function () {
  return {
    oncreate () {
      //this.on('', () => {

      //});
    },

    data () {
      return {
        menuItems: [],
        menuItemsFlat: [],
        isOpen: false,
        prevY: 0
      }
    },

    methods: {
      onAppScroll (y) {
        let menuItems = this.get('menuItemsFlat');
        
        for(let [key, item] of menuItems.entries()) {
          let el = document.getElementById(item.id);
          let elemTop = el.getBoundingClientRect().top;
          let elemBottom = el.getBoundingClientRect().bottom;

          // Is on screen
          if ((elemTop >= 0) && (elemBottom <= window.innerHeight)) {
            // Remove active from all menu items
            [...document.querySelectorAll('.menu li.active')].map(i => i.classList.remove('active'));

            // Get item to highlight
            let menuToHighlight = document.getElementById(`menu-${item.id}`);
            
            // Highlight item
            menuToHighlight.classList.add('active');

            let sectionToShow = menuToHighlight.parentNode;

            // If its an item inside subsection, make subsection (father) visible
            if(sectionToShow.classList.contains('subsection')) {
              [...document.querySelectorAll('.menu .subsection')].map(i => i.classList.remove('show'));
              sectionToShow.classList.add('show');
            }

            // If its an item father of a subsection, make subsection (child) visible
            if(menuToHighlight.querySelectorAll('.subsection').length > 0) {
              [...document.querySelectorAll('.menu .subsection')].map(i => i.classList.remove('show'));
              menuToHighlight.querySelector('.subsection').classList.add('show');
            }

            // Stop loop
            break;
          }
        }
      },
      startMenu (menuOpts) {
        this.set(menuOpts);
        setTimeout(() => {
          this.scrollToURLMenu();
        }, 10);
      },
      scrollTop() {
        window.scrollTo(0, 0);
        this.close();
      },
      scrollToURLMenu () {
        let hash = window.location.hash.substr(2);
        let filtered = this.get('menuItemsFlat').filter(item => item.id === hash);

        if (filtered.length > 0) {
          let heading = document.getElementById(filtered[0].id);
          let top = window.scrollY + heading.getBoundingClientRect().top - 70;
          window.scrollTo(0, top);
        } else {
          window.scrollTo(0, 1);
        }
      },
      toggle () {
        this.set({isOpen: !this.refs.menu.isOpen});
        return this.refs.menu.isOpen;
      },
      close () {
        this.set({isOpen: false});
      },
      open () {
        this.set({isOpen: true});
      },
      scrollToItem (item, e) {
        if(e) {
          e.preventDefault();
        }

        window.location.hash = '/' + item.id;
        let menuItems = this.get('menuItemsFlat');
        let top = window.scrollY + document.getElementById(item.id).getBoundingClientRect().top - 70;
        window.scrollTo(0, top);

        this.fire('mobilemenu', {action: 'CLOSE'});
      }
    }
  }
}());

function create_main_fragment ( state, component ) {
	var div_1_class_value, div_2_class_value;

	function onwindowscroll ( event ) {
		component.onAppScroll(this.scrollY);
	};
	window.addEventListener( 'scroll', onwindowscroll );

	var text = createText( "\n" );
	var div = createElement( 'div' );
	var div_1 = createElement( 'div' );
	appendNode( div_1, div );
	div_1.className = div_1_class_value = "MenuComp--overlay " + ( (state.isOpen) ? 'active' : '' );

	function click_handler ( event ) {
		component.fire('mobilemenu', {action: 'CLOSE'});
	}

	addListener( div_1, 'click', click_handler );
	component.refs.overlay = div_1;
	appendNode( createText( "\n  " ), div );
	var div_2 = createElement( 'div' );
	appendNode( div_2, div );
	div_2.className = div_2_class_value = "MenuComp " + ( (state.isOpen) ? 'open' : '' );
	component.refs.menu = div_2;
	var div_3 = createElement( 'div' );
	appendNode( div_3, div_2 );
	div_3.className = "logo";

	function click_handler_1 ( event ) {
		component.scrollTop();
	}

	addListener( div_3, 'click', click_handler_1 );
	var img = createElement( 'img' );
	appendNode( img, div_3 );
	img.src = "img/logo.svg";
	img.alt = "PeerAssets WhitePaper";
	appendNode( createText( "\n    " ), div_2 );
	var ul = createElement( 'ul' );
	appendNode( ul, div_2 );
	ul.className = "menu";
	var each_block_value = state.menuItems;

	var each_block_iterations = [];

	for ( var i = 0; i < each_block_value.length; i += 1 ) {
		each_block_iterations[i] = create_each_block( state, each_block_value, each_block_value[i], i, component );
		each_block_iterations[i].mount( ul, null );
	}

	return {
		mount: function ( target, anchor ) {
			insertNode( text, target, anchor );
			insertNode( div, target, anchor );
		},

		update: function ( changed, state ) {
			if ( div_1_class_value !== ( div_1_class_value = "MenuComp--overlay " + ( (state.isOpen) ? 'active' : '' ) ) ) {
				div_1.className = div_1_class_value;
			}

			if ( div_2_class_value !== ( div_2_class_value = "MenuComp " + ( (state.isOpen) ? 'open' : '' ) ) ) {
				div_2.className = div_2_class_value;
			}

			var each_block_value = state.menuItems;

			if ( 'menuItems' in changed ) {
				for ( var i = 0; i < each_block_value.length; i += 1 ) {
					if ( each_block_iterations[i] ) {
						each_block_iterations[i].update( changed, state, each_block_value, each_block_value[i], i );
					} else {
						each_block_iterations[i] = create_each_block( state, each_block_value, each_block_value[i], i, component );
						each_block_iterations[i].mount( ul, null );
					}
				}

				for ( ; i < each_block_iterations.length; i += 1 ) {
					each_block_iterations[i].unmount();
					each_block_iterations[i].destroy();
				}
				each_block_iterations.length = each_block_value.length;
			}
		},

		unmount: function () {
			detachNode( text );
			detachNode( div );

			for ( var i = 0; i < each_block_iterations.length; i += 1 ) {
				each_block_iterations[i].unmount();
			}
		},

		destroy: function () {
			window.removeEventListener( 'scroll', onwindowscroll );

			removeListener( div_1, 'click', click_handler );
			if ( component.refs.overlay === div_1 ) component.refs.overlay = null;
			if ( component.refs.menu === div_2 ) component.refs.menu = null;
			removeListener( div_3, 'click', click_handler_1 );

			destroyEach( each_block_iterations, false, 0 );
		}
	};
}

function create_each_block ( state, each_block_value, item, item_index, component ) {
	var li_id_value, li_class_value, a_href_value, text_value;

	var li = createElement( 'li' );
	li.id = li_id_value = "menu-" + ( item.id );
	li.className = li_class_value = item.type;
	var a = createElement( 'a' );
	appendNode( a, li );
	a.href = a_href_value = "#" + ( item.id );
	addListener( a, 'click', click_handler );

	a._svelte = {
		component: component,
		each_block_value: each_block_value,
		item_index: item_index
	};

	var text = createText( text_value = item.label );
	appendNode( text, a );
	appendNode( createText( "\n            " ), li );

	var if_block = (item.children.length > 0) && create_if_block( state, each_block_value, item, item_index, component );

	if ( if_block ) if_block.mount( li, null );

	return {
		mount: function ( target, anchor ) {
			insertNode( li, target, anchor );
		},

		update: function ( changed, state, each_block_value, item, item_index ) {
			if ( li_id_value !== ( li_id_value = "menu-" + ( item.id ) ) ) {
				li.id = li_id_value;
			}

			if ( li_class_value !== ( li_class_value = item.type ) ) {
				li.className = li_class_value;
			}

			if ( a_href_value !== ( a_href_value = "#" + ( item.id ) ) ) {
				a.href = a_href_value;
			}

			a._svelte.each_block_value = each_block_value;
			a._svelte.item_index = item_index;

			if ( text_value !== ( text_value = item.label ) ) {
				text.data = text_value;
			}

			if ( item.children.length > 0 ) {
				if ( if_block ) {
					if_block.update( changed, state, each_block_value, item, item_index );
				} else {
					if_block = create_if_block( state, each_block_value, item, item_index, component );
					if_block.mount( li, null );
				}
			} else if ( if_block ) {
				if_block.unmount();
				if_block.destroy();
				if_block = null;
			}
		},

		unmount: function () {
			detachNode( li );
			if ( if_block ) if_block.unmount();
		},

		destroy: function () {
			removeListener( a, 'click', click_handler );
			if ( if_block ) if_block.destroy();
		}
	};
}

function create_each_block_1 ( state, each_block_value, item, item_index, each_block_value_1, subitem, subitem_index, component ) {
	var li_id_value, li_class_value, a_href_value, text_value;

	var li = createElement( 'li' );
	li.id = li_id_value = "menu-" + ( subitem.id );
	li.className = li_class_value = subitem.type;
