			insertNode( li, target, anchor );
		},

		update: function ( changed, state, each_block_value, item, item_index, each_block_value_1, subitem, subitem_index ) {
			if ( li_id_value !== ( li_id_value = "menu-" + ( subitem.id ) ) ) {
				li.id = li_id_value;
			}

			if ( li_class_value !== ( li_class_value = subitem.type ) ) {
				li.className = li_class_value;
			}

			if ( a_href_value !== ( a_href_value = "#" + ( subitem.id ) ) ) {
				a.href = a_href_value;
			}

			a._svelte.each_block_value_1 = each_block_value_1;
			a._svelte.subitem_index = subitem_index;

			if ( text_value !== ( text_value = subitem.label ) ) {
				text.data = text_value;
			}
		},

		unmount: function () {
			detachNode( li );
		},

		destroy: function () {
			removeListener( a, 'click', click_handler_1 );
		}
	};
}

function create_if_block ( state, each_block_value, item, item_index, component ) {
	var ul = createElement( 'ul' );
	ul.className = "subsection";
	var each_block_value_1 = item.children;

	var each_block_1_iterations = [];

	for ( var i = 0; i < each_block_value_1.length; i += 1 ) {
		each_block_1_iterations[i] = create_each_block_1( state, each_block_value, item, item_index, each_block_value_1, each_block_value_1[i], i, component );
		each_block_1_iterations[i].mount( ul, null );
	}

	return {
		mount: function ( target, anchor ) {
			insertNode( ul, target, anchor );
		},

		update: function ( changed, state, each_block_value, item, item_index ) {
			var each_block_value_1 = item.children;

			if ( 'menuItems' in changed ) {
				for ( var i = 0; i < each_block_value_1.length; i += 1 ) {
					if ( each_block_1_iterations[i] ) {
						each_block_1_iterations[i].update( changed, state, each_block_value, item, item_index, each_block_value_1, each_block_value_1[i], i );
					} else {
						each_block_1_iterations[i] = create_each_block_1( state, each_block_value, item, item_index, each_block_value_1, each_block_value_1[i], i, component );
						each_block_1_iterations[i].mount( ul, null );
					}
				}

				for ( ; i < each_block_1_iterations.length; i += 1 ) {
					each_block_1_iterations[i].unmount();
					each_block_1_iterations[i].destroy();
				}
				each_block_1_iterations.length = each_block_value_1.length;
			}
		},

		unmount: function () {
			detachNode( ul );

			for ( var i = 0; i < each_block_1_iterations.length; i += 1 ) {
				each_block_1_iterations[i].unmount();
			}
		},

		destroy: function () {
			destroyEach( each_block_1_iterations, false, 0 );
		}
	};
}

function click_handler ( event ) {
	var component = this._svelte.component;
	var each_block_value = this._svelte.each_block_value, item_index = this._svelte.item_index, item = each_block_value[item_index];
	component.scrollToItem(item, event);
}

function click_handler_1 ( event ) {
	var component = this._svelte.component;
	var each_block_value_1 = this._svelte.each_block_value_1, subitem_index = this._svelte.subitem_index, subitem = each_block_value_1[subitem_index];
	component.scrollToItem(subitem, event);
}

function SvelteComponent ( options ) {
