			detachBetween( raw_before, raw_after );

			detachNode( div );
		},

		destroy: function () {
			if ( component.refs.topbar === topbar ) component.refs.topbar = null;
			topbar.destroy( false );
			if ( component.refs.menu === menu ) component.refs.menu = null;
